import { useSelector } from 'react-redux';
import AOS from "aos"
import "aos/dist/aos.css"
import cathe from "../image/CATHERc.png"
import IKEA from "../image/IKEAc.png"
import SUGAR from '../image/SUGARcc.png'
import html from "../image/icons8-html-5-48.png"
import css from '../image/icons8-css3-48.png'
import js from '../image/icons8-javascript-48.png'
import redux from "../image/icons8-redux-48.png"
import react from '../image/react.jpeg'
import chakra from "../image/chakra.png"
import { useEffect } from 'react';
function Project(){
    const data=useSelector((store)=> store.theme)
    useEffect(()=>{
        AOS.init({ duration: 1000 });
    },[])
    let projectdata={
        projects:[
            {
                image:SUGAR,
                title:'SUGAR (CLONE)',
                type:'An E-commers Website',
                description:"SUGAR is an E-commers website responsible for providing affordable cosmetics for women's. It is a leading Indian cosmetic brand that was created with the goal of providing high-quality, affordable makeup products to women everywhere.",
                texttack:[html,css,js,react,redux,chakra],
                code:'CODE',
                live:'LIVE'
            },
            {
                image:cathe,
                title:'CATHERINES (CLONE)',
                type:'An E-commers Website',
                description:"Catherines is an E-commers website responsible for providing affordable plus size fashion products for women's to all over the world. Catherines was created with the goal of providing high-quality, affordable products to women everywhere.",
                texttack:[html,css,js],
                code:'CODE',
                live:'LIVE'
            },
            {
                image:IKEA,
                title:'IKEA (CLONE)',
                type:"An E-commers Website",
                description:"IKEA is an E-commers website responsible for providing affordable home decore, furiture and various other stuffs to all over the world. IKEA, home furnishings retailer that was the worlds largest seller of furniture in the early 21st century, operating more than 300 stores around the world.",
                texttack:[html,css,js],
                code:'CODE',
                live:'LIVE'
            },
            {
                image:IKEA,
                title:'IKEA (CLONE)',
                type:"An E-commers Website",
                description:"IKEA is an E-commers website responsible for providing affordable home decore, furiture and various other stuffs to all over the world. IKEA, home furnishings retailer that was the worlds largest seller of furniture in the early 21st century, operating more than 300 stores around the world.",
                texttack:[html,css,js],
                code:'CODE',
                live:'LIVE'
            },
        ]
    }
    return (
        <div style={{
        padding:'0px 10px',
        width:'100%',
        display:'flex',justifyContent:'center',alignItems:'center',flexDirection:'column'
        }}  id='projects'>
           <div data-aos="zoom-in-down"
        data-aos-duration="1000"
        data-aos-offset="100" style={{fontSize:'37px',backgroundColor:'#4b8118',color:'white',borderRadius:'15px',padding:'0px 25px',paddingTop:'0px',marginTop:'90px',marginBottom:'70px'}}> <h1 ><u>projects</u></h1></div>
           <div style={{width:'85%'}}>{projectdata.projects.map((item)=>
            <div data-aos="zoom-in-down"
            data-aos-duration="2000"
            data-aos-offset="100" style={{display:'flex',padding:'10px 10px',marginBottom:'40px',borderRadius:'15px',boxShadow: '#4b8118 5px 3px 15px'}}>
                <div style={{width:'50%'}}><img style={{display:'block',borderRadius:'10px'}} src={item.image} alt="" /></div>
                <br/>
                <div style={{width:'50%',textAlign:'left',display:'flex',flexDirection:'column',alignItems:'center',padding:'0px 25px'}}>
                    <h1 style={{fontSize:'20px'}}>{item.title}</h1>
                    <br/>
                    <div>
                    <p>{item.type}</p>
                    <p>{item.description}</p>
                    <div  style={{display:'flex',justifyContent:'space-around',alignItems:'center',padding:'3% 0%'}}>{item.texttack.map((ele)=> <img width='50px'  src={ele} alt='err'/>)}</div>
                    <div  style={{display:'flex',gap:'20px',padding:'0px 20px'}}>
                        <button data-aos="flip-left" style={{padding:'5px 15px',backgroundColor:'#4b8118',borderRadius:'10px'}}>CODE</button>
                        <button data-aos="flip-right" style={{padding:'5px 15px',backgroundColor:'#4b8118',borderRadius:'10px'}}>LIVE</button>
                    </div>
                    </div>
                </div>
            </div>
           )}</div>
           


        </div>
    )
}

export default Project